import { Send } from "lucide-react";

const MessageInput = ({ newMessage, setNewMessage, handleSendMessage }) => (
  <div className="mt-4">
    <div className="flex">
      <input
        type="text"
        value={newMessage}
        onChange={(e) => setNewMessage(e.target.value)}
        onKeyDown={(e) => e.key === "Enter" && handleSendMessage(e)}
        placeholder="پیام خود را بنویسید..."
        className="flex-1 p-3 border border-gray-300 rounded-r-lg focus:outline-none"
      />
      <button
        onClick={handleSendMessage}
        className="bg-blue-500 text-white rounded-l-lg p-3 hover:bg-blue-600"
      >
        <Send className="w-5 h-5" />
      </button>
    </div>
  </div>
);

export default MessageInput;
