import React, { useState, useEffect, useRef, useCallback, memo } from "react";
import { useParams, useNavigate } from "react-router-dom";
import {
  Ticket,
  ArrowRight,
  Clock,
  User,
  MessageCircle,
  Tag,
  CheckCircle,
  AlertCircle,
  XCircle,
  Menu,
  X,
  Send,
  Paperclip,
} from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import MessageInput from "./MessageInput";
import MessageItem from "./MessageItem";
import TicketDetails from "./TicketDetails";
// import Messa

const statusColors = {
  open: { bg: "bg-blue-100", text: "text-blue-800", icon: AlertCircle },
  pending: { bg: "bg-yellow-100", text: "text-yellow-800", icon: Clock },
  resolved: { bg: "bg-green-100", text: "text-green-800", icon: CheckCircle },
  closed: { bg: "bg-gray-100", text: "text-gray-800", icon: XCircle },
};

const ViewTicket = () => {
  const { ticketId } = useParams();
  const navigate = useNavigate();
  const [ticket, setTicket] = useState(null);
  const [newMessage, setNewMessage] = useState("");
  const [loading, setLoading] = useState(true);
  const [messages, setMessages] = useState([]);
  const [isSticky, setIsSticky] = useState(false);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const messagesEndRef = useRef(null);

  const API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/tickets/"
      : "https://allmaxsport.com/api/tickets/";

  useEffect(() => {
    async function loadTicketDetails() {
      const response = await fetch(`${API_URL}?id=${ticketId}`);
      const data = await response.json();

      setTicket(data[0]);

      setMessages(data[0].messages);
      setLoading(false);
    }

    loadTicketDetails();

    // Add scroll event listener for sticky header
    const handleScroll = () => {
      const scrollPosition = window.scrollY;
      setIsSticky(scrollPosition > 100);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, [ticketId]);

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  const handleStatusChange = (newStatus) => {
    // Update the ticket state
    setTicket((prev) => ({
      ...prev,
      status: newStatus,
      lastUpdated: new Date().toISOString(),
    }));

    // Send the updated status to your API
    fetch(`${API_URL}?id=${ticket.id}`, {
      method: "PATCH",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        status: newStatus,
        updated_at: new Date().toISOString(),
      }),
    })
      .then((res) => {
        if (!res.ok) {
          return res.text().then((text) => {
            throw new Error(text);
          });
        }
        return res.json();
      })
      .then((data) => console.log("Status updated successfully:", data))
      .catch((error) => console.error("Error updating status:", error));
  };

  const handleSendMessage = (e) => {
    e.preventDefault();
    if (newMessage?.trim()) {
      const newMsg = {
        id: messages?.length + 1,
        ticket: ticketId,
        text: newMessage,
        sender: "admin",
        message: newMessage,
        timestamp: new Date().toISOString(),
        isUser: false,
      };

      setMessages((prev) => [...prev, newMsg]);
      setNewMessage("");

      fetch(`${API_URL}?id=${ticketId}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(newMsg),
      })
        .then((res) => {
          if (!res.ok) {
            return res.text().then((text) => {
              throw new Error(text);
            });
          }
          return res.json();
        })
        .then((data) => console.log("new message Data: ", data))
        .catch((error) => console.error("Error:", error));
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen p-4">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-t-blue-500 border-blue-200 rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-400">در حال بارگذاری اطلاعات تیکت...</p>
        </div>
      </div>
    );
  }

  const StatusIcon = statusColors[ticket.status]?.icon || AlertCircle;

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-zinc-900 relative">
      {/* Sticky Header */}
      <header
        className={`sticky top-0 z-20 transition-all duration-300 ${
          isSticky ? "bg-white dark:bg-zinc-800 shadow-md" : "bg-transparent"
        } py-3 px-4`}
      >
        <div className="max-w-7xl mx-auto flex justify-between items-center">
          <div className="flex items-center">
            <motion.button
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              onClick={() => navigate("/dashboard/tickets")}
              className="flex items-center mr-3 text-gray-600 dark:text-gray-300 hover:text-blue-600 dark:hover:text-blue-400"
            >
              <ArrowRight className="w-5 h-5" />
              <span className="hidden md:inline mr-1">بازگشت</span>
            </motion.button>

            <button
              onClick={() => setSidebarOpen(!sidebarOpen)}
              className="md:hidden mr-2 text-gray-600 dark:text-gray-300"
            >
              <Menu className="w-5 h-5" />
            </button>

            <h1 className="text-lg md:text-xl font-bold text-gray-800 dark:text-gray-100 flex items-center">
              <Ticket className="w-5 h-5 md:w-6 md:h-6 ml-1 text-blue-500" />
              <span className="truncate max-w-[150px] md:max-w-full">
                تیکت #{ticket.id}
              </span>
            </h1>
          </div>

          <div className="flex items-center space-x-2 space-x-reverse">
            <div
              className={`${statusColors[ticket.status]?.bg || "bg-gray-100"} ${
                statusColors[ticket.status]?.text || "text-gray-800"
              } px-2 py-1 rounded-full text-xs md:text-sm font-medium flex items-center`}
            >
              <StatusIcon className="w-3 h-3 md:w-4 md:h-4 ml-1" />
              {ticket.status === "open"
                ? "باز"
                : ticket.status === "pending"
                ? "در انتظار"
                : ticket.status === "resolved"
                ? "حل شده"
                : "بسته شده"}
            </div>

            <div className="hidden sm:flex px-2 py-1 bg-red-100 text-red-800 rounded-full text-xs md:text-sm font-medium items-center">
              <Tag className="w-3 h-3 md:w-4 md:h-4 ml-1" />
              {ticket.priority}
            </div>
          </div>
        </div>
      </header>

      {/* Mobile Sidebar */}
      <div
        className={`fixed inset-0 bg-zinc bg-opacity-50 z-30 md:hidden transition-opacity duration-300 ${
          sidebarOpen ? "opacity-100" : "opacity-0 pointer-events-none"
        }`}
        onClick={() => setSidebarOpen(false)}
      ></div>

      <div
        className={`fixed top-0 right-0 h-full w-3/4 max-w-xs bg-zinc dark:bg-zinc-800 shadow-xl z-40 md:hidden transform transition-transform duration-300 ${
          sidebarOpen ? "translate-x-0" : "translate-x-full"
        }`}
      >
        <div className="p-4 flex justify-between items-center border-b border-zinc-200 dark:border-zinc-700">
          <h2 className="text-lg font-bold text-gray-800 dark:text-gray-100">
            اطلاعات تیکت
          </h2>
          <button
            onClick={() => setSidebarOpen(false)}
            className="text-gray-500"
          >
            <X className="w-5 h-5" />
          </button>
        </div>

        <div className="p-4 overflow-y-auto">
          <TicketDetails
            ticket={ticket}
            handleStatusChange={handleStatusChange}
          />
        </div>
      </div>

      <main className="p-3 md:p-6 max-w-7xl mx-auto">
        {/* Ticket subject banner */}
        <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm mb-4 overflow-hidden">
          <div className="bg-zinc-50 dark:bg-zinc-900/30 p-3 md:p-4 border-b border-zinc-100 dark:bg-zinc-800">
            <h2 className="text-md md:text-lg font-semibold text-gray-800 dark:text-gray-100 line-clamp-2">
              {ticket?.subject}
            </h2>
            <div className="flex flex-wrap items-center text-xs md:text-sm text-gray-500 dark:text-gray-400 mt-2 gap-y-2 gap-x-3">
              <div className="flex items-center">
                <User className="w-3 h-3 md:w-4 md:h-4 ml-1" />
                {ticket?.customer_name}
              </div>
              <div className="flex items-center">
                <MessageCircle className="w-3 h-3 md:w-4 md:h-4 ml-1" />
                {messages?.length} پیام
              </div>
              <div className="flex items-center">
                <Clock className="w-3 h-3 md:w-4 md:h-4 ml-1" />
                {new Date(ticket.created_at).toLocaleDateString("fa-IR")}
              </div>
            </div>
          </div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 md:gap-6">
          {/* Ticket info sidebar - Desktop */}
          <div className="hidden md:block">
            <div className="bg-white dark:bg-zinc-800 rounded-lg shadow-sm p-4 md:p-6">
              <TicketDetails
                ticket={ticket}
                handleStatusChange={handleStatusChange}
              />
            </div>
          </div>

          {/* Messages area */}
          <div className="md:col-span-2 flex flex-col bg-white dark:bg-zinc-800 rounded-lg shadow-sm overflow-hidden h-[calc(100vh-240px)] md:h-[700px]">
            <div className="flex-grow overflow-y-auto p-3 md:p-4 space-y-4">
              <AnimatePresence mode="wait">
                {messages?.map((message) => (
                  <MessageItem
                    key={message.id}
                    message={message}
                    customerName={ticket.customer_name}
                  />
                ))}
              </AnimatePresence>
              <div ref={messagesEndRef} />
            </div>

            <div className="border-t border-gray-200 dark:border-zinc-700 p-3 md:p-4">
              <MessageInput
                newMessage={newMessage}
                setNewMessage={setNewMessage}
                handleSendMessage={handleSendMessage}
              />
            </div>
          </div>
        </div>
      </main>
    </div>
  );
};

export default ViewTicket;
