const TicketDetails = ({ ticket, handleStatusChange }) => (
  <div>
    <h3 className="text-lg font-semibold text-gray-100 mb-4">جزئیات تیکت</h3>
    <div className="space-y-4">
      <div className="bg-zinc-700 py-4 px-6 rounded-lg">
        <p className="text-sm text-gray-100">کاربر : </p>
        <p className="text-white">{ticket?.customer_name}</p>
      </div>
      <div className="bg-zinc-700 py-4 px-6 rounded-lg">
        <p className="text-sm text-gray-100">موضوع</p>
        <p className="text-white">{ticket.subject}</p>
      </div>

      <div className="bg-zinc-700 py-4 px-6 rounded-lg">
        <p className="text-sm text-gray-100">وضعیت</p>
        <div className="relative">
          <select
            value={ticket.status}
            onChange={(e) => handleStatusChange(e.target.value)}
            className="appearance-none w-full p-2 border border-gray-300 rounded-lg bg-zinc-700 text-gray-100 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition duration-150 ease-in-out pr-8"
          >
            <option value="pending" className="bg-zinc-700 text-gray-100">
              در انتظار
            </option>
            <option value="open" className="bg-zinc-700 text-gray-100">
              باز
            </option>
            <option value="closed" className="bg-zinc-700 text-gray-100">
              بسته
            </option>
          </select>
        </div>
      </div>

      <div className="bg-zinc-700 py-4 px-6 rounded-lg">
        <p className="text-sm text-gray-100">ایجاد شده</p>
        <p className="text-white">
          {new Date(ticket.created_at).toLocaleString("fa-IR")}
        </p>
      </div>
      <div className="bg-zinc-700 py-4 px-6 rounded-lg">
        <p className="text-sm text-gray-100">آخرین به‌روزرسانی</p>
        <p className="text-white">
          {new Date(ticket.updated_at).toLocaleString("fa-IR")}
        </p>
      </div>
    </div>
  </div>
);

export default TicketDetails;
